up"}},[_c('h2',[_c('span',[_vm._v("1.2")]),_vm._v(" Esquemas del delito y su impacto en la punibilidad")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 order-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/10.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("El análisis dogmático del delito ha sido sistematizado a lo largo del tiempo mediante diferentes esquemas teóricos, que no solo facilitan la comprensión de cómo se configura una conducta punible, sino que también tienen efectos prácticos directos en la aplicación de la ley penal. Estos esquemas permiten determinar si una conducta es sancionable y bajo qué condiciones, convirtiéndose en herramientas esenciales para la evaluación de la responsabilidad penal y la toma de decisiones judiciales.")]),_c('div',{staticClass:"bg-color-2 p-4",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Uno de los modelos más aceptados en la doctrina penal contemporánea, especialmente en la legislación colombiana (Ley 599 de 2000), es el esquema tripartito del delito, estructurado en tres elementos fundamentales: tipicidad, antijuridicidad y culpabilidad. Este modelo, influenciado por la teoría finalista, conserva también aspectos de las teorías clásica y neoclásica, reflejando una visión moderna e integral del delito.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/11.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('h2',{attrs:{"data-aos":"fade-left"}},[_vm._v("Elementos del esquema tripartito")]),_c('p',{staticClass:"mb-5"},[_vm._v("El primer componente es la tipicidad, que se refiere a la adecuación de una conducta a un tipo penal previamente establecido por la ley. Solo puede considerarse delito aquella acción que encaje perfectamente en la descripción normativa. Por ejemplo, tomar un bien ajeno con ánimo de apropiación, sin consentimiento y con valor económico, configura el delito de hurto (art. 239 C.P.). Si no hay tipicidad, no hay delito ni punibilidad.")]),_c('div',{staticClass:"bg-color-3 p-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row align-items-start"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema1/12.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("El segundo elemento es la antijuridicidad, que exige que la conducta típica sea además contraria al ordenamiento jurídico, es decir, que no concurra ninguna causa de justificación. Si alguien golpea a otro, se configura una lesión personal; sin embargo, si actúa en legítima defensa, la antijuridicidad desaparece, lo que excluye la posibilidad de imponer una pena.")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-full-width bg-color-6 mb-5"},[_c('div',{staticClass:"px-4 px-md-5"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-lg-3 mb-3 mb-lg-0 order-lg-2"},[_c('img',{staticClass:"mx-auto",attrs:{"src":require("@/assets/curso/tema1/13.svg"),"data-aos":"zoom-in"}})]),_c('div',{staticClass:"col-lg-9 order-lg-1"},[_c('p',{staticClass:"mb-0 text-white"},[_vm._v("El tercer elemento es la culpabilidad, entendida como la posibilidad de reprochar al autor su conducta. Para ello, se requiere que la persona sea imputable, que haya actuado con conciencia de la ilicitud y que se le pueda exigir un comportamiento distinto. Por ejemplo, si alguien comete un delito en estado de trastorno mental severo, aunque la conducta sea típica y antijurídica, no puede ser culpable y, por lo tanto, no es punible. En tales casos, el ordenamiento prevé medidas de seguridad en lugar de penas (Congreso Internacional de Jóvenes Investigadores en Ciencias Penales, 2012).")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/14.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('h2',{attrs:{"data-aos":"fade-left"}},[_vm._v("Impacto en la punibilidad")]),_c('h5',[_vm._v("El impacto de este esquema es directo y determinante:")]),_c('h5',[_vm._v("La ausencia de cualquiera de los tres elementos excluye la posibilidad de imponer una pena.")]),_c('p',[_vm._v("Por tanto, este análisis estructurado guía al juez en la valoración probatoria, en la calificación jurídica del hecho y en la individualización de la sanción, respetando el debido proceso y los derechos fundamentales del imputado.")]),_c('div',{staticClass:"bg-color-3 p-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row align-items-start"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema1/15.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("Además, el esquema tripartito no solo permite decidir si una conducta es punible, sino también cómo debe ser sancionada. En el proceso penal, sirve como base para evaluar agravantes, atenuantes, culpabilidad reducida o inimputabilidad, y para aplicar medidas de seguridad o subrogados penales de manera justa y proporcional.")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row align-items-center bg-color-1"},[_c('div',{staticClass:"col-lg-2 order-lg-2 mb-3 mb-lg-0"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema1/16.svg")}})]),_c('div',{staticClass:"col-lg-10 order-lg-1"},[_c('p',{staticClass:"mb-0 p-4"},[_vm._v("En conclusión, los esquemas del delito no son simples construcciones teóricas, sino herramientas metodológicas indispensables que orientan el ejercicio legítimo del ius puniendi del Estado, garantizando una justicia penal racional, sistemática y respetuosa de los derechos humanos.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-12 col-md-6 col-lg-3 offset-lg-1"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/componentes/material-complementario.svg"),"alt":""}})])])}]

